<template>
  <div>
    <transition mode="out-in">
      <base-card v-if="toggleBtnText === 'login'">
        <h2>Sign Up</h2>
        <signup-form />
      </base-card>
      <base-card v-else>
        <login-form />
      </base-card>
    </transition>
    <div class="formatBtn">
      <base-button @click="changeText">{{ btnText }}</base-button>
    </div>
  </div>
</template>

<script>
import SignupForm from '../UI/SignupForm.vue';
import LoginForm from '../UI/LoginForm.vue';
export default {
  components: { SignupForm, LoginForm },
  data() {
    return {
      toggleBtnText: 'login'
    };
  },
  computed: {
    btnText() {
      return `Switch to ${this.toggleBtnText}`;
    }
  },
  methods: {
    changeText() {
      if (this.toggleBtnText === 'login') {
        this.toggleBtnText = 'signup';
      } else {
        this.toggleBtnText = 'login';
      }
    }
  }
};
</script>

<style scoped>
.formatBtn {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

.v-enter-from {
  transform: translateX(-30px);
  opacity: 0;
}
.v-enter-to,
.v-leave-from {
  transform: translateX(0px);
  opacity: 1;
}
.v-enter-active {
  transition: all 0.3s ease-in;
}

.v-leave-to {
  transform: translateX(30px);
  opacity: 0;
}
.v-leave-active {
  transition: all 0.3s ease-out;
}
</style>
