export default {
  /* sampleMutation(state, payload) {
    state.coachesArray = payload[0];
    console.log(state.coachesArray[0]);
  } */
  addNewCoach(state, payload) {
    state.coachesArrayNew.push(payload);
  },
  coachRegister(state) {
    state.isRegistered = true;
  },
  getAllCoaches(state, payload) {
    state.coachesArrayNew = payload;
  },
  setFetchTimeStamp(state) {
    state.lastFetch = new Date().getTime();
  }
};
