<template>
  <base-card>
    <section>
      <h2>Find Your Coach</h2>
      <span class="filter-option">
        <input
          type="checkbox"
          name="frontend"
          id="frontend"
          value="frontend"
          @change="setFilter"
          checked
        />
        <label for="frontend">Frontend</label>
      </span>
      <span class="filter-option">
        <input
          type="checkbox"
          name="backend"
          id="backend"
          value="backend"
          @change="setFilter"
          checked
        />
        <label for="backend">Backend</label>
      </span>
      <span class="filter-option">
        <input
          type="checkbox"
          name="career"
          id="career"
          value="career"
          @change="setFilter"
          checked
        />
        <label for="career">Career</label>
      </span>
    </section>
  </base-card>
</template>

<script>
export default {
  emits: ['emitFilter'],
  data() {
    return {
      filter: {
        frontend: true,
        backend: true,
        career: true
      },
      selectedCoaches: []
    };
  },
  methods: {
    setFilter(event) {
      const checkboxId = event.target.id;
      const isActive = event.target.checked;
      const updatedFilter = { ...this.filter, [checkboxId]: isActive };
      this.filter = updatedFilter;
      this.$emit('emitFilter', updatedFilter);
    }
  }
};
</script>

<style scoped>
h2 {
  margin: 0.5rem 0;
}

.filter-option {
  margin-right: 1rem;
}

.filter-option label,
.filter-option input {
  vertical-align: middle;
}

.filter-option label {
  margin-left: 0.25rem;
}

.filter-option.active label {
  font-weight: bold;
}
</style>
