import axios from 'axios';

const coachUrl = '/api/v1/coach';
const requestUrl = '/api/v1/request';

/////////////////////////
// COACH
/////////////////////////

export const createCoach = async (coach, token) => {
  try {
    const res = await axios({
      method: 'post',
      url: coachUrl,
      data: {
        firstName: coach.firstName,
        lastName: coach.lastName,
        areas: coach.areas,
        description: coach.description,
        hourlyRate: coach.hourlyRate,
        userId: coach.userId
      },
      headers: { authorization: `Bearer ${token}` }
    });

    return res;
  } catch (err) {
    return err;
  }
};

export const getAllCoaches = async () => {
  try {
    const res = await axios({
      method: 'get',
      url: coachUrl
    });

    return res;
  } catch (err) {
    return err;
  }
};

//////////////////////////
// REQUEST
//////////////////////////

export const createRequest = async request => {
  try {
    const res = await axios({
      method: 'post',
      url: requestUrl,
      data: {
        userEmail: request.userEmail,
        userMessage: request.userMessage,
        coachSlug: request.coachSlug
      }
    });

    return res;
  } catch (err) {
    return err;
  }
};

export const getAllRequests = async token => {
  try {
    const res = await axios({
      method: 'get',
      url: requestUrl,
      headers: { authorization: `Bearer ${token}` },
      withCredentials: true
    });

    return res;
  } catch (err) {
    return err;
  }
};

///////////////////////
// AUTHENTICATE
///////////////////////

export const signupRequest = async userData => {
  const res = await axios({
    method: 'post',
    url: '/api/v1/signup',
    data: {
      name: userData.name,
      email: userData.email,
      password: userData.password,
      confirmPassword: userData.confirmPassword
    },
    withCredentials: true
  });

  return res;
};

export const loginRequest = async userData => {
  const res = await axios({
    method: 'post',
    url: '/api/v1/login',
    data: {
      email: userData.email,
      password: userData.password
    },
    withCredentials: true
  });

  return res;
};
