<template>
  <div>
    <base-dialog
      :show="!!error"
      title="Something Happened..."
      @close="closeDialog"
      >{{ error }}</base-dialog
    >
    <base-card>
      <header>
        <h2>Request Received</h2>
      </header>
      <div v-if="isLoading">
        <base-spinner></base-spinner>
      </div>
      <h3 v-else-if="!hasRequest">You currently don't have any request.</h3>
      <ul v-else>
        <request-item
          v-for="request in returnRequest"
          :key="request.userEmail"
          :coachId="request.coachSlug"
          :userEmail="request.userEmail"
          :userMessage="request.userMessage"
        ></request-item>
      </ul>
    </base-card>
  </div>
</template>

<script>
import RequestItem from '../UI/RequestItems.vue';
export default {
  components: {
    RequestItem
  },
  data() {
    return {
      isLoading: false,
      error: null
    };
  },
  computed: {
    returnRequest() {
      return this.$store.getters['request/getRequest'];
    },
    hasRequest() {
      return this.$store.getters['request/hasRequest'];
    }
  },
  methods: {
    async getAllRequests() {
      this.isLoading = true;
      try {
        await this.$store.dispatch('request/getAllRequests');
      } catch (err) {
        console.log(err.message);
        this.error = err.message;
      }
      this.isLoading = false;
    },
    closeDialog() {
      this.error = null;
    }
  },
  created() {
    this.getAllRequests();
  }
};
</script>

<style scoped>
header {
  text-align: center;
}

ul {
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  max-width: 30rem;
}

h3 {
  text-align: center;
}
</style>
