import { createRequest, getAllRequests } from '../../../backendConnect';

export default {
  async addRequest({ commit }, payload) {
    const newRequest = {
      userEmail: payload.email,
      userMessage: payload.message,
      coachSlug: payload.coachSlug
    };

    const res = await createRequest(newRequest);

    if (res.status === 200) {
      commit('addRequest', res.data.data);
    } else {
      const error = new Error(res.message || 'Failed to send the message');
      throw error;
    }
  },
  async getAllRequests({ commit, rootGetters }) {
    const token = rootGetters.outputToken;
    const res = await getAllRequests(token);
    if (res.status === 200) {
      commit('getAllRequests', res.data.data);
    } else {
      const error = new Error(res.message || 'Failed to fetch the data.');
      throw error;
    }
  }
};
