import { signupRequest, loginRequest } from '../backendConnect';

export default {
  async authBruh({ commit }, payload) {
    if (payload.mode === 'signup') {
      const res = await signupRequest(payload);
      if (res.status === 200) {
        // localStorage.setItem() is not SAFE!
        // let your server add secure httpOnly cookies

        // get the token duration from the cookie 'document.cookie'.

        // add a code that will run 'this.dispatch('logout')' inside a callback function of a setTimeout function everytime you login/signup.

        commit('setAuth', res.data);
      }
    }

    if (payload.mode === 'login') {
      const res = await loginRequest(payload);
      if (res.status === 200) {
        commit('setAuth', res.data);
      }
    }
  },

  logout({ commit }) {
    // add the clearTimeout() that will clear the setTimeout in the login/signup

    // remove the cookies
    commit('logout');
  }
};
