import { createRouter, createWebHistory } from 'vue-router';

import CoachList from './components/pages/CoachList.vue';
import CoachDetails from './components/pages/CoachDetails.vue';
import CoachContact from './components/pages/CoachContact.vue';
import CoachRegister from './components/pages/CoachRegister.vue';
import RequestReceived from './components/pages/RequestReceived.vue';
import Authentication from './components/pages/Authentication.vue';
import ErrorMessage from './components/pages/ErrorMessage.vue';

import rootStore from './store/index';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', redirect: '/coaches' },
    { path: '/coaches', component: CoachList },
    {
      path: '/coaches/:slug',
      component: CoachDetails,
      children: [{ path: 'contact', component: CoachContact }]
    },
    { path: '/register', component: CoachRegister, meta: {requiresAuth: true} },
    { path: '/request', component: RequestReceived, meta: {requiresAuth: true} },
    { path: '/authenticate', component: Authentication, meta: {requiresNoAuth: true} },
    { path: '/:anything(.*)', component: ErrorMessage }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !rootStore.getters.isAuthenticated ) {
    next('/authenticate');
  } else {
    next();
  }

  if (to.meta.requiresNoAuth && rootStore.getters.isAuthenticated) {
    next('/')
  }
})

export default router;
