<template>
  <form @submit.prevent="sendMessage">
    <div class="form-control" :class="{ invalid: !userEmail.isValid }">
      <label for="email">Your Email Address:</label>
      <p v-if="!userEmail.isValid">{{ userEmail.errorMsg }}</p>
      <input
        type="email"
        id="email"
        name="email"
        v-model="userEmail.val"
        @blur="clearValidity('userEmail')"
      />
    </div>
    <div class="form-control" :class="{ invalid: !userMessage.isValid }">
      <label for="message">Message:</label>
      <p v-if="!userMessage.isValid">{{ userMessage.errorMsg }}</p>
      <textarea
        name="message"
        id="message"
        rows="5"
        v-model.trim="userMessage.val"
        @blur="clearValidity('userMessage')"
      ></textarea>
    </div>
    <div class="actions">
      <base-button>Send Message</base-button>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      formIsValid: true,
      userEmail: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      userMessage: {
        val: '',
        isValid: true,
        errorMsg: 'Please add a message'
      },
      error: ''
    };
  },
  methods: {
    clearValidity(input) {
      if (input === 'userEmail') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your email address';
        } else if (!this[input].val.includes('@')) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add a valid email address';
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'userMessage') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
        } else {
          this[input].isValid = true;
        }
      }
    },
    formValid() {
      this.formIsValid = true;

      if (this.userEmail.val === '' || !this.userEmail.val.includes('@')) {
        this.formIsValid = false;
      }
      if (this.userMessage.val === '') {
        this.formIsValid = false;
      }
    },
    sendMessage() {
      try {
        this.formValid();

        const requestObj = {
          coachSlug: this.$route.params.slug,
          email: this.userEmail.val,
          message: this.userMessage.val
        };

        if (!this.formIsValid) return;
        this.$store.dispatch('request/addRequest', requestObj);
        this.$router.replace('/');
      } catch (err) {
        this.error = err.message;
        throw this.error;
      }
    }
  }
};
</script>

<style scoped>
form {
  margin: 1rem;
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  padding: 0.15rem;
}

input:focus,
textarea:focus {
  border-color: #3d008d;
  background-color: #faf6ff;
  outline: none;
}

.errors {
  font-weight: bold;
  color: red;
}

.actions {
  text-align: center;
}

.invalid label,
.invalid p {
  color: red;
}

.invalid input,
.invalid textarea {
  border: 1px solid red;
}
</style>
