<template>
  <base-dialog fixed :show="isLoading" title="Authenticating...">
    <base-spinner></base-spinner
  ></base-dialog>
  <h2>Login</h2>
  <form @submit.prevent="submitForm">
    <div class="form-control" :class="{ invalid: !email.isValid }">
      <label for="email">Email Address:</label>
      <input
        type="email"
        id="email"
        v-model.trim="email.val"
        @blur="clearValidity('email')"
      />
      <p v-if="!email.isValid">{{ email.errorMsg }}</p>
    </div>
    <div class="form-control" :class="{ invalid: !password.isValid }">
      <label for="password">Password:</label>
      <input
        type="password"
        id="password"
        v-model.trim="password.val"
        @blur="clearValidity('password')"
      />
      <p v-if="!password.isValid">{{ password.errorMsg }}</p>
    </div>
    <base-button>Submit</base-button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      formIsValid: true,
      email: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      password: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      isLoading: false,
      error: ''
    };
  },
  methods: {
    clearValidity(input) {
      if (input === 'email') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your email address';
        } else if (!this[input].val.includes('@')) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add a valid email address';
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'password') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your password';
        } else if (this[input].val.length < 8) {
          this[input].isValid = false;
          this[input].errorMsg =
            'Your password should be at least 8 characters long';
        } else {
          this[input].isValid = true;
        }
      }
    },
    validateFields() {
      this.formIsValid = true;

      if (!this.email.val.includes('@') || this.email.val === '') {
        this.email.isValid = false;
        this.formIsValid = false;
        return;
      }

      if (this.password.val.length < 8 || this.password.val === '') {
        this.password.isValid = false;
        this.formIsValid = false;
        return;
      }
    },
    async submitForm() {
      try {
        this.validateFields();

        if (!this.formIsValid) {
          return;
        }

        const loginUser = {
          email: this.email.val,
          password: this.password.val,
          mode: 'login'
        };

        console.log(loginUser);
        this.isLoading = true;
        await this.$store.dispatch('authBruh', loginUser);

        this.isLoading = false;

        console.log({
          token: this.$store.state.token,
          userID: this.$store.state.userId
        });

        this.$router.replace('/');
      } catch (err) {
        this.isLoading = false;
        console.log(err);
        if (err.message.includes('401')) {
          this.error = 'Wrong email or password.';
        } else {
          this.error = 'Failed to fetch';
        }
        throw this.error;
      }
    }
  }
};
</script>

<style scoped>
form {
  margin: 1rem;
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  padding: 0.15rem;
}

input:focus,
textarea:focus {
  border-color: #3d008d;
  background-color: #faf6ff;
  outline: none;
}

.errors {
  font-weight: bold;
  color: red;
}

.actions {
  text-align: center;
}

.invalid label,
.invalid p {
  color: red;
}

.invalid input,
.invalid textarea {
  border: 1px solid red;
}
</style>
