<template>
  <div>
    <section>
      <base-card>
        <h2>{{ fullName }}</h2>
        <h3>${{ rate }}/hour</h3>
      </base-card>
    </section>
    <section>
      <base-card>
        <h2>Interested? Reach out now!</h2>
        <base-button link :to="contactLink" v-if="contactBtnIsVisible"
          >Contact</base-button
        >
        <router-view></router-view>
      </base-card>
    </section>
    <section>
      <base-card>
        <base-badge
          v-for="area of areas"
          :key="area"
          :badgeName="area"
        ></base-badge>
        <p>{{ description }}</p>
      </base-card>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedCoach: null
    };
  },
  computed: {
    contactBtnIsVisible() {
      if (this.$route.path.includes('contact')) {
        return false;
      } else {
        return true;
      }
    },
    checkPath() {
      return this.$route.fullPath.includes('contact');
    },
    fullName() {
      return `${this.selectedCoach.firstName} ${this.selectedCoach.lastName}`;
    },
    rate() {
      return this.selectedCoach.hourlyRate;
    },
    contactLink() {
      return `${this.$route.path}/contact`;
    },
    areas() {
      return this.selectedCoach.areas;
    },
    description() {
      return this.selectedCoach.description;
    }
  },
  created() {
    const param = this.$route.params.slug;
    this.selectedCoach = this.$store.getters['coach/showAllCoaches'].find(
      coach => coach.slug === param
    );
    if (!this.selectedCoach) {
      this.$router.push('/error');
    }
  }
};
</script>
