<template>
  <base-dialog v-if="isLoading" fixed title="Authenticating...">
    <base-spinner></base-spinner>
  </base-dialog>
  <form @submit.prevent="submitForm">
    <div class="form-control" :class="{ invalid: !fullName.isValid }">
      <label for="name">Name:</label>
      <input
        type="text"
        id="name"
        v-model.trim="fullName.val"
        @blur="clearValidity('fullName')"
      />
      <p v-if="!fullName.isValid">{{ fullName.errorMsg }}</p>
    </div>
    <div class="form-control" :class="{ invalid: !email.isValid }">
      <label for="email">Email Address:</label>
      <input
        type="email"
        id="email"
        v-model.trim="email.val"
        @blur="clearValidity('email')"
      />
      <p v-if="!email.isValid">{{ email.errorMsg }}</p>
    </div>
    <div class="form-control" :class="{ invalid: !password.isValid }">
      <label for="password">Password:</label>
      <input
        type="password"
        id="password"
        v-model.trim="password.val"
        @blur="clearValidity('password')"
      />
      <p v-if="!password.isValid">{{ password.errorMsg }}</p>
    </div>
    <div class="form-control" :class="{ invalid: !confirmPassword.isValid }">
      <label for="confirmPassword">Confirm Password:</label>
      <input
        type="password"
        id="confirmPassword"
        v-model.trim="confirmPassword.val"
        @blur="clearValidity('confirmPassword')"
      />
      <p v-if="!confirmPassword.isValid">{{ confirmPassword.errorMsg }}</p>
    </div>
    <base-button>Submit</base-button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      formIsValid: true,
      fullName: {
        val: '',
        isValid: true,
        errorMsg: 'Please add your name'
      },
      email: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      password: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      confirmPassword: {
        val: '',
        isValid: true,
        errorMsg: ''
      },
      isLoading: false,
      error: ''
    };
  },
  methods: {
    clearValidity(input) {
      if (input === 'email') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your email address';
        } else if (!this[input].val.includes('@')) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add a valid email address';
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'fullName') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'password') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your password';
        } else if (this[input].val.length < 8) {
          this[input].isValid = false;
          this[input].errorMsg =
            'Your password should be at least 8 characters long';
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'confirmPassword') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this[input].errorMsg = 'Please add your password again';
        } else if (this[input].val !== this.password.val) {
          this[input].isValid = false;
          this[input].errorMsg = 'Your password is not the same';
        } else {
          this[input].isValid = true;
        }
      }
    },
    validateFields() {
      this.formIsValid = true;

      if (this.fullName.val === '') {
        this.fullName.isValid = false;
        this.formIsValid = false;
        return;
      }

      if (!this.email.val.includes('@') || this.email.val === '') {
        this.email.isValid = false;
        this.formIsValid = false;
        return;
      }

      if (this.password.val.length < 8 || this.password.val === '') {
        this.password.isValid = false;
        this.formIsValid = false;
        return;
      }

      if (
        this.confirmPassword.val !== this.password.val ||
        this.confirmPassword === ''
      ) {
        this.confirmPassword.isValid = false;
        this.formIsValid = false;
        return;
      }
    },
    async submitForm() {
      try {
        this.validateFields();

        if (!this.formIsValid) {
          return;
        }

        const newUser = {
          name: this.fullName.val,
          email: this.email.val,
          password: this.password.val,
          confirmPassword: this.confirmPassword.val,
          mode: 'signup'
        };

        this.isLoading = true;
        await this.$store.dispatch('authBruh', newUser);
        this.isLoading = false;

        this.$router.replace('/');
      } catch (err) {
        this.isLoading = false;
        console.log(err);
        this.error = 'Failed to fetch';
        throw this.error;
      }
    }
  }
};
</script>

<style scoped>
form {
  margin: 1rem;
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  padding: 0.15rem;
}

input:focus,
textarea:focus {
  border-color: #3d008d;
  background-color: #faf6ff;
  outline: none;
}

.errors {
  font-weight: bold;
  color: red;
}

.actions {
  text-align: center;
}

.invalid label,
.invalid p {
  color: red;
}

.invalid input,
.invalid textarea {
  border: 1px solid red;
}
</style>
