<template>
  <div>
    <section>
      <coach-filter @emitFilter="addFilters"></coach-filter>
    </section>
    <section>
      <base-card>
        <div class="controls">
          <base-button mode="outline" @click="getAllCoaches(true)">
            Refresh
          </base-button>
          <base-button
            v-if="isAuthenticated && !checkIfRegistered"
            to="/register"
            link
            >Register as Coach</base-button
          >
        </div>
        <div v-if="isLoading">
          <base-spinner></base-spinner>
        </div>
        <ul v-else-if="!hasCoaches">
          <coach-items
            v-for="coach of filteredCoaches"
            :key="coach._id"
            :slug="coach.slug"
            :firstName="coach.firstName"
            :lastName="coach.lastName"
            :areas="coach.areas"
            :description="coach.description"
            :hourlyRate="coach.hourlyRate"
          ></coach-items>
        </ul>
        <h3 v-else>No Coaches Found</h3>
      </base-card>
    </section>
  </div>
</template>

<script>
import CoachItems from '../UI/CoachItems.vue';
import CoachFilter from '../UI/CoachFilter.vue';

export default {
  components: {
    CoachItems,
    CoachFilter
  },
  data() {
    return {
      filter: {
        frontend: true,
        backend: true,
        career: true
      },
      isLoading: false,
      error: null
    };
  },
  computed: {
    filteredCoaches() {
      const allCoaches = this.$store.getters['coach/showAllCoaches'];
      return allCoaches.filter(coach => {
        let coachAreas = coach.areas;

        if (this.filter.frontend && coachAreas.includes('frontend')) {
          return true;
        }
        if (this.filter.backend && coachAreas.includes('backend')) {
          return true;
        }
        if (this.filter.career && coachAreas.includes('career')) {
          return true;
        }
        return false;
      });
    },
    hasCoaches() {
      return this.$store.getters['coach/hasCoaches'];
    },
    isRegistered() {
      return this.$store.getters['coach/checkIfRegistered'];
    },
    getFetchTimeStamp() {
      return this.$store.getters['coach/fetchTimeStamp'];
    },
    checkIfRegistered() {
      const userId = this.$store.getters.outputUserId;

      const singleUser = this.$store.getters['coach/showAllCoaches'].find(
        el => el.userId === userId
      );

      if (!singleUser) {
        return false;
      } else {
        return true;
      }
    },
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    }
  },
  methods: {
    addFilters(filterObj) {
      this.filter = filterObj;
    },
    async getAllCoaches(refreshBtn) {
      this.isLoading = true;
      try {
        await this.$store.dispatch('coach/getAllCoaches', refreshBtn);
      } catch (err) {
        this.error = err.message;
        throw this.error;
      }
      this.isLoading = false;
    }
  },
  async created() {
    await this.getAllCoaches(false);
  }
};
</script>

<style scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.controls {
  display: flex;
  justify-content: space-between;
}
</style>
