<template>
  <div>
    <section>
      <base-card>
        <h2>Register as a coach</h2>
        <coach-form @formData="addToState"></coach-form>
      </base-card>
    </section>
  </div>
</template>

<script>
import CoachForm from '../UI/CoachForm.vue';
export default {
  components: {
    CoachForm
  },
  data() {
    return {
      error: null
    };
  },
  methods: {
    async addToState(formData) {
      try {
        await this.$store.dispatch('coach/addNewCoach', formData);
        this.$router.replace('/');
      } catch (err) {
        this.error = err.message;
        throw this.error;
      }
    }
  }
};
</script>

