  async addNewCoach({ commit, rootGetters }, payload) {
    const coachData = {
      firstName: payload.first,
      lastName: payload.last,
      description: payload.desc,
      hourlyRate: payload.rate,
      areas: payload.areas,
      userId: rootGetters.outputUserId
    };

    const token = rootGetters.outputToken;

    const res = await createCoach(coachData, token);

    console.log(res);

    if (res.status === 200) {
      commit('addNewCoach', res);
    } else {
      const error = new Error(res.message || 'Failed to fetch');
      throw error;
    }
  },
  coachRegister({ commit }) {
    commit('coachRegister');
  },
  async getAllCoaches({ commit, getters }, payload) {
    if (!getters.fetchTimeStamp && !payload) {
      return;
    }

    const res = await getAllCoaches();

    commit('getAllCoaches', res.data.data);
  }
