<template>
  <form @submit.prevent="registerCoach">
    <div class="form-control" :class="{ invalid: !firstName.isValid }">
      <label for="firstName">First Name:</label>
      <input
        type="text"
        id="firstName"
        v-model.trim="firstName.val"
        :placeholder="firstName.isValid ? '' : firstName.errorMsg"
        @blur="clearValidity('firstName')"
      />
    </div>
    <div class="form-control" :class="{ invalid: !lastName.isValid }">
      <label for="lastName">Last Name:</label>
      <input
        type="text"
        id="lastName"
        v-model.trim="lastName.val"
        :placeholder="lastName.isValid ? '' : lastName.errorMsg"
        @blur="clearValidity('lastName')"
      />
    </div>
    <div class="form-control" :class="{ invalid: !areas.isValid }">
      <h3>Areas of Expertise:</h3>
      <div class="form-control">
        <input
          type="checkbox"
          id="areas"
          value="frontend"
          v-model="areas.val"
          @blur="clearValidity('areas')"
        />
        <label for="frontend">Frontend</label>
      </div>
      <div class="form-control">
        <input
          type="checkbox"
          id="areas"
          value="backend"
          v-model="areas.val"
          @blur="clearValidity('areas')"
        />
        <label for="backend">Backend</label>
      </div>
      <div class="form-control">
        <input
          type="checkbox"
          id="areas"
          value="career"
          v-model="areas.val"
          @blur="clearValidity('areas')"
        />
        <label for="career">Career</label>
      </div>
      <p v-if="!areas.isValid">Please add at least one area of expertise.</p>
    </div>
    <div class="form-control" :class="{ invalid: !description.isValid }">
      <label for="description">Description</label>
      <textarea
        name="description"
        id="description"
        rows="5"
        v-model.trim="description.val"
        :placeholder="description.isValid ? '' : description.errorMsg"
        @blur="clearValidity('description')"
      ></textarea>
    </div>
    <div class="form-control" :class="{ invalid: !rate.isValid }">
      <label for="hourlyRate">Hourly Rate:</label>
      <input
        type="number"
        id="hourlyRate"
        v-model.number="rate.val"
        @blur="clearValidity('rate')"
      />
      <p v-if="!rate.isValid">Please add a rate</p>
    </div>
    <div class="form-control">
      <base-button>Register</base-button>
    </div>
  </form>
</template>

<script>
export default {
  emits: ['formData'],
  data() {
    return {
      formIsValid: true,
      firstName: {
        val: '',
        isValid: true,
        errorMsg: 'Please add your first name.'
      },
      lastName: {
        val: '',
        isValid: true,
        errorMsg: 'Please add your last name.'
      },
      rate: {
        val: null,
        isValid: true,
        errorMsg: 'Please add a rate.'
      },
      description: {
        val: '',
        isValid: true,
        errorMsg: 'Please add a description.'
      },
      areas: {
        val: [],
        isValid: true
      },
      error: ''
    };
  },
  methods: {
    clearValidity(input) {
      if (input === 'areas') {
        if (this[input].val.length === 0) {
          this[input].isValid = false;
          this.formIsValid = false;
        } else {
          this[input].isValid = true;
        }
      } else if (input === 'rate') {
        if (!this[input].val || this[input].val < 0) {
          this[input].isValid = false;
          this.formIsValid = false;
        } else {
          this[input].isValid = true;
        }
      } else {
        if (this[input].val === '') {
          this[input].isValid = false;
          this.formIsValid = false;
        } else {
          this[input].isValid = true;
        }
      }
    },
    formValidate() {
      this.formIsValid = true;

      if (this.firstName.val === '') {
        this.firstName.isValid = false;
        this.formIsValid = false;
      }

      if (this.lastName.val === '') {
        this.lastName.isValid = false;
        this.formIsValid = false;
      }

      if (this.description.val === '') {
        this.description.isValid = false;
        this.formIsValid = false;
      }

      if (!this.rate.val || this.rate.val < 0) {
        this.rate.isValid = false;
        this.formIsValid = false;
      }

      if (this.areas.val.length === 0) {
        this.areas.isValid = false;
        this.formIsValid = false;
      }
    },
    registerCoach() {
      try {
        this.formValidate();

        if (!this.formIsValid) {
          return;
        }

        const formData = {
          first: this.firstName.val,
          last: this.lastName.val,
          desc: this.description.val,
          rate: this.rate.val,
          areas: this.areas.val
        };
        this.$emit('formData', formData);
        //this.$store.dispatch('coach/coachRegister');
        //this.$router.replace('/');
      } catch (err) {
        this.error = err.message;
        throw this.error;
      }
    }
  }
};
</script>

<style scoped>
.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input[type='checkbox'] + label {
  font-weight: normal;
  display: inline;
  margin: 0 0 0 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  font: inherit;
}

input:focus,
textarea:focus {
  background-color: #f0e6fd;
  outline: none;
  border-color: #3d008d;
}

input[type='checkbox'] {
  display: inline;
  width: auto;
  border: none;
}

input[type='checkbox']:focus {
  outline: #3d008d solid 1px;
}

h3 {
  margin: 0.5rem 0;
  font-size: 1rem;
}

.invalid label,
.invalid p {
  color: red;
}

.invalid input,
.invalid textarea {
  border: 1px solid red;
}
</style>
