import { createStore } from 'vuex';
import rootActions from './rootActions.js';
import rootMutations from './rootMutations.js';
import rootGetters from './rootGetters.js';

import coachesIndex from './modules/coaches/index.js';
import requestIndex from './modules/requests/index.js';

const store = createStore({
  modules: {
    coach: coachesIndex,
    request: requestIndex
  },
  state() {
    return {
      userId: null,
      token: null,
      firstName: null
    };
  },
  mutations: rootMutations,
  actions: rootActions,
  getters: rootGetters
});

export default store;
