export default {
  showAllCoaches(state) {
    return state.coachesArrayNew;
  },
  hasCoaches(state) {
    return state.coachesArrayNew && state.coachesArrayNew > 0;
  },
  /* checkIfRegistered(state) {
    return state.isRegistered;
  } ,*/
  fetchTimeStamp(state) {
    const lastFetchTime = state.lastFetch;
    if (!lastFetchTime) {
      return true;
    }
    const timeNow = new Date().getTime();
    const diff = timeNow - lastFetchTime;
    return diff / 1000 > 60;
  }
};
